<template>
  <div class="game"
    :class="{
      'game--ready': game.phase === 'ready',
      'game--running': game.phase === 'running',
      'game--dead': game.phase === 'dying' || game.phase === 'gameover'
    }"
  >
    <canvas id="game" ref="canvasRef"></canvas>

    <div class="parallax" ref="parallaxRef">
      <div class="layer layer--0" data-duration="9">
        <div class="layer__track"></div>
      </div>
      <div class="layer layer--1" data-duration="10">
        <div class="layer__track"></div>
      </div>
      <div class="layer layer--2" data-duration="11">
        <div class="layer__track"></div>
      </div>
      <div class="layer layer--3" data-duration="12">
        <div class="layer__track"></div>
      </div>
      <div class="layer layer--4" data-duration="4">
        <div class="layer__track"></div>
      </div>
    </div>

    <div class="gui">
      <div class="gui__score">
        <div class="gui-icon gui-icon--score"></div> <span></span> <span class="score-value">{{ game.coinCount }}</span>
      </div>
      <div class="gui__life">
        <div class="gui-life">
          <div
            v-for="i in Math.max(0, Math.min(3, game.lifeCount))"
            :key="i"
            class="gui-icon gui-icon--life"
          ></div>
        </div>
      </div>
      <div class="gui__sound">
        <div
          class="gui-button"
          :class="{ muted: isMuted }"
          @click="onToggleMute"
          role="button"
          aria-label="Toggle sound"
          :aria-pressed="isMuted"
        >
          <div class="gui-icon gui-icon--sound"></div>
        </div>
      </div>
      <div class="gui__pause">
        <div
          class="gui-button"
          :class="{ paused: isPaused }"
          @click="onTogglePause"
          role="button"
          aria-label="Toggle pause"
          :aria-pressed="isPaused"
        >
          <div class="gui-icon gui-icon--pause"></div>
        </div>
      </div>
      <Transition name="vfx-fade">
        <div v-if="isOverlayVisible" class="gui__overlay">
          <Transition name="vfx-fade" mode="out-in">
            <button
              v-if="currentButton === 'start'"
              class="gui-button gui-button--start"
              @click="onStartClick"
              key="start"
            >
              <div class="gui-icon gui-icon--start"></div>
            </button>
            <button
              v-else-if="currentButton === 'restart'"
              class="gui-button gui-button--restart"
              @click="onRestartClick"
              key="restart"
            >
              <div class="gui-icon gui-icon--restart"></div>
            </button>
          </Transition>
        </div>
      </Transition>
    </div>
  </div>
</template>

<script setup lang="ts">
import { onMounted, onUnmounted, ref, computed, watch } from 'vue';
import { initGame, pauseGame, resumeGame, isGamePaused, startGame, restartGame } from '@/game/hopper';
import { initCssParallax } from '@/lib/parallax';
import { getAudioEngine } from '@/audio/engine';
import { useGameStore } from '@/stores/game';

const canvasRef = ref<HTMLCanvasElement | null>(null);
const parallaxRef = ref<HTMLDivElement | null>(null);
const game = useGameStore();
const isMuted = computed(() => game.muted);
const isPaused = computed(() => game.isPaused);
const isOverlayVisible = computed(() => game.phase !== 'running');
const currentButton = computed<'start' | 'restart' | null>(() => {
  if (game.phase === 'idle' || game.phase === 'ready') {
    return 'start';
  }
  if (game.phase === 'dying' || game.phase === 'gameover') {
    return 'restart';
  }
  return null;
});

let dispose: (() => void) | null = null;
let parallaxCtrl: ReturnType<typeof initCssParallax> | null = null;

function onToggleMute(): void {
  const audio = getAudioEngine();
  const next = !isMuted.value;

  try { audio.setMuted(next); } catch {}
  try { game.setMuted(next); } catch {}
}

function onTogglePause(): void {
  const next = !isPaused.value;

  try {
    if (next) {
      pauseGame();

      if (parallaxCtrl?.pause) parallaxCtrl.pause();

      else if (parallaxCtrl?.stop) { try { parallaxCtrl.stop(); } catch {} ; parallaxCtrl = null; }
    } else {
      resumeGame();

      if (game.phase !== 'idle' && game.phase !== 'ready') {
        if (parallaxCtrl?.resume) parallaxCtrl.resume();

        else if (!parallaxCtrl && parallaxRef.value) {
          parallaxCtrl = initCssParallax(parallaxRef.value);
        }
      }
    }
  } catch {}
  try { if (next) game.pause(); else game.resume(); } catch {}
}

onMounted(() => {
  if (canvasRef.value) {
    dispose = initGame(
      canvasRef.value,
      () => {
        if (parallaxCtrl?.stop) { try { parallaxCtrl.stop(); } catch {} ; parallaxCtrl = null; }
      },
      () => {
        if (!isPaused.value && parallaxRef.value) {
          parallaxCtrl = initCssParallax(parallaxRef.value);
        }
      }
    );
  }
  try { game.setMuted(getAudioEngine().isMuted()); } catch {}
  try { if (isGamePaused()) game.pause(); } catch {}
});

onUnmounted(() => {
  if (dispose) dispose();
  if (parallaxCtrl?.stop) parallaxCtrl.stop();
});

function onStartClick(): void {
  try { startGame(); } catch {}
}

function onRestartClick(): void {
  try { restartGame(); } catch {}
}

</script>

