/*  Vue <Transition name="vfx-fade"> */
:root {
  --vfx-dur: 1s;
  --vfx-ease: ease-in;
}
.vfx-fade-enter-active,
.vfx-fade-leave-active {
  transition: opacity var(--vfx-dur) var(--vfx-ease);
}
.vfx-fade-enter-from,
.vfx-fade-leave-to {
  opacity: 0;
}
/*      Restart */
.gui-button--restart.vfx-fade-enter-active {
  transition-delay: 2s;
  pointer-events: none;
}
/*      restart */
.gui-button--restart.vfx-fade-leave-active {
  pointer-events: none;
}
/*   overlay:  +     */
.gui__overlay.vfx-fade-enter-active,
.gui__overlay.vfx-fade-leave-active {
  transition:
    opacity var(--vfx-dur) var(--vfx-ease),
    background-color var(--vfx-dur) var(--vfx-ease);
}
.gui__overlay.vfx-fade-enter-from,
.gui__overlay.vfx-fade-leave-to {
  background-color: rgba(3, 9, 11, 0);
}
@media (prefers-reduced-motion: reduce) {
  .vfx-fade-enter-active,
  .vfx-fade-leave-active {
    transition: none !important;
  }
}






