// Worker-first.  fallback-   .
import type { WorkerInMessage, WorkerOutMessage } from '@/types/workerMessages';
import { getAudioEngine } from '@/audio/engine';
import { useGameStore } from '@/stores/game';

type Controls = {
  pause: () => void;
  resume: () => void;
  isPaused: () => boolean;
  start?: () => void;
  restart?: () => void;
};
let currentControls: Controls | null = null;
export function pauseGame(): void { try { currentControls?.pause(); } catch {} }
export function resumeGame(): void { try { currentControls?.resume(); } catch {} }
export function isGamePaused(): boolean { try { return currentControls?.isPaused() ?? false; } catch { return false; } }
export function startGame(): void { try { currentControls?.start?.(); } catch {} }
export function restartGame(): void { try { currentControls?.restart?.(); } catch {} }

export function initGame(canvas: HTMLCanvasElement, onGameOver?: () => void, onGameStart?: () => void) {
  // ---   ---
  const gameStore = useGameStore();
  const hasOffscreen = typeof (canvas as any).transferControlToOffscreen === 'function';
  const hasWorker = typeof Worker !== 'undefined';
  const canWorkerMode = hasOffscreen && hasWorker;
  const useWorker = canWorkerMode;

  if (useWorker) {
    canvas.style.touchAction = 'none';
    (canvas.style as any).webkitTapHighlightColor = 'transparent';
    (canvas.style as any).webkitUserSelect = 'none';

    // --- Web Audio engine (SFX/BGM) ---
    const audio = getAudioEngine();
    let audioUnlocked = false;
    const ensureAudioUnlocked = () => {
      if (audioUnlocked) return;
      audioUnlocked = true;
      //      ;   then
      void audio.unlock().catch(() => {});
    };
    const playJumpSfx = () => audio.playSfx('jump');
    const playBlightSfx = () => audio.playSfx('blight');
    const playSkeletonSfx = () => audio.playSfx('skeleton');
    const playCoinSfx = () => audio.playSfx('coin');
    const playHeartSfx = () => audio.playSfx('heart');
    const playDeathSfx = () => audio.playSfx('death');

    // UI    :    

    // :  computedStyle  ,  
    const rawBg = getComputedStyle(canvas).backgroundColor;
    const isTransparentCss = !rawBg || rawBg === 'rgba(0, 0, 0, 0)' || rawBg === 'transparent';
    const skyColor =
      !isTransparentCss ? rawBg : '#e0f7fa';

    const offscreen = (canvas as any).transferControlToOffscreen() as OffscreenCanvas;
    const worker = new Worker(new URL('./worker.entry.ts', import.meta.url), { type: 'module' }) as Worker & {
      postMessage: (msg: WorkerInMessage, transfer?: Transferable[]) => void;
      onmessage: ((this: Worker, ev: MessageEvent<WorkerOutMessage>) => any) | null;
    };

    const containerEl = canvas.parentElement as HTMLElement | null;

    let lastCssW = -1;
    let lastCssH = -1;
    let debugHitboxes = false;
    const sendResize = (rect: DOMRectReadOnly) => {
      const dpr = window.devicePixelRatio || 1;
      const w = Math.round(rect.width);
      const h = Math.round(rect.height);
      if (w === lastCssW && h === lastCssH) return;
      lastCssW = w; lastCssH = h;
      worker.postMessage({ type: 'resize', cssW: w, cssH: h, dpr });
    };

    //    viewport (iOS  /DPR    )
    let vvTimer: number | null = null;
    const onVV = () => {
      if (vvTimer) { clearTimeout(vvTimer); vvTimer = null; }
      vvTimer = window.setTimeout(() => {
        const r = (containerEl ?? canvas).getBoundingClientRect();
        sendResize(r);
        vvTimer = null;
      }, 140);
    };
    const vv = (window as any).visualViewport as VisualViewport | undefined;
    if (vv) {
      vv.addEventListener('resize', onVV);
      vv.addEventListener('scroll', onVV);
    }

    let roPending = false;
    const ro = new ResizeObserver((entries) => {
      if (roPending) return;
      roPending = true;
      const entry = entries[0];
      const rect = entry?.contentRect;
      requestAnimationFrame(() => {
        if (rect) sendResize(rect);
        else {
          const r = (containerEl ?? canvas).getBoundingClientRect();
          sendResize(r);
        }
        roPending = false;
      });
    });
    ro.observe(containerEl ?? canvas);

    // :     resize ( )
    {
      const r = (containerEl ?? canvas).getBoundingClientRect();
      const dpr = window.devicePixelRatio || 1;
      debugHitboxes = (() => {
        try { return new URLSearchParams(location.search).get('hitboxes') === '1'; } catch { return false; }
      })();
      worker.postMessage(
        {
          type: 'init',
          canvas: offscreen,
          cssW: Math.round(r.width),
          cssH: Math.round(r.height),
          dpr,
          skyColor,
          expectResize: true,
          alpha: isTransparentCss,
          transparentBg: isTransparentCss,
          debugHitboxes,
        },
        [offscreen as any]
      );
    }

    // rAF-     'ready'  
    let rafId = 0;
    let started = false;
    let isReady = false;
    let startRequested = false;
    let paused = false;
    const shouldAutoStart = (() => {
      try {
        // : sessionStorage-  URL ?autostart=1
        const key = 'hopper_autostart';
        const ss = sessionStorage.getItem(key);
        if (ss === '1') {
          sessionStorage.removeItem(key);
          return true;
        }
        return new URLSearchParams(location.search).get('autostart') === '1';
      } catch {
        return false;
      }
    })();
    let startOverlayEl: HTMLDivElement | null = null;
    const drive = (t: number) => {
      worker.postMessage({ type: 'frame', now: t });
      rafId = requestAnimationFrame(drive);
    };
    const applyPause = (nextPaused: boolean) => {
      if (!started) { paused = nextPaused; return; }
      if (nextPaused === paused) return;
      paused = nextPaused;
      try { if (paused) gameStore.pause(); else gameStore.resume(); } catch {}
      try { worker.postMessage({ type: 'vis', hidden: paused }); } catch {}
      if (paused) {
        try { cancelAnimationFrame(rafId); } catch {}
        try { audio.handleVisibility(true); } catch {}
      } else {
        try { rafId = requestAnimationFrame(drive); } catch {}
        try { audio.handleVisibility(false); } catch {}
      }
    };
    let isGameOver = false;
    const start = () => {
      if (started) return;
      started = true;
      //      /
      try { worker.postMessage({ type: 'vis', hidden: (document.hidden || paused) }); } catch {}
      try { if (!paused) gameStore.start(); } catch {}
      if (!paused) {
        rafId = requestAnimationFrame(drive);
      }
    };
    const onVis = () => {
      if (!started) return;
      worker.postMessage({ type: 'vis', hidden: document.hidden });
      if (document.hidden) {
        cancelAnimationFrame(rafId);
        audio.handleVisibility(true);
      } else {
        if (!paused) {
          rafId = requestAnimationFrame(drive);
          audio.handleVisibility(false);
        }
      }
    };
    document.addEventListener('visibilitychange', onVis);

    worker.onmessage = (ev: MessageEvent<WorkerOutMessage>) => {
      const data = ev.data || ({} as WorkerOutMessage);
      if (data.type === 'ui') {
        const coin = typeof (data as any).coinCount === 'number' ? (data as any).coinCount : gameStore.coinCount;
        const life = typeof (data as any).lifeCount === 'number' ? (data as any).lifeCount : gameStore.lifeCount;
        try { gameStore.setCounts(coin, life); } catch {}
      } else if (data.type === 'ready') {
        isReady = true;
        try { gameStore.setPhase('ready'); } catch {}
        if (startRequested && !started) start();
			} else if (data.type === 'sfx') {
        if (data.s === 'jump') {
          playJumpSfx();
        } else if (data.s === 'blight') {
          playBlightSfx();
        } else if (data.s === 'skeleton') {
          playSkeletonSfx();
        } else if (data.s === 'coin') {
          playCoinSfx();
        } else if (data.s === 'heart') {
          playHeartSfx();
        }
      } else if ((data as any).type === 'death') {
        //      ( rAF     )
        try { if (typeof onGameOver === 'function') onGameOver(); } catch {}
        try { audio.stopBgm(); } catch {}
        playDeathSfx();
        try { gameStore.markDeath(); } catch {}
      } else if ((data as any).type === 'gameover') {
        if (isGameOver) return;
        isGameOver = true;
        try { cancelAnimationFrame(rafId); } catch {}
        try { audio.stopBgm(); } catch {}
        paused = false;
        try { if (typeof onGameOver === 'function') onGameOver(); } catch {}
        try { gameStore.markGameOver(); } catch {}
      }
    };

    //   
    const onKeyDown = (e: KeyboardEvent) => {
      if (e.code === 'Space') {
        if (paused) { e.preventDefault(); return; }
        if (started) {
          worker.postMessage({ type: 'input', space: true });
        }
      }
    };
    window.addEventListener('keydown', onKeyDown);
    const onTap = () => {
      if (paused) return;
      ensureAudioUnlocked();
      requestAnimationFrame(() => worker.postMessage({ type: 'input', tap: true }));
    };
    const onClickCapture = (e: MouseEvent) => e.preventDefault();
    canvas.addEventListener('pointerdown', onTap, { passive: true });
    canvas.addEventListener('click', onClickCapture, { passive: false, capture: true });

    //    ( )
    if (shouldAutoStart) {
      startRequested = true;
      ensureAudioUnlocked();
      void audio.startBgm();
      if (typeof onGameStart === 'function') { try { onGameStart(); } catch {} }
      try { gameStore.start(); } catch {}
      if (isReady && !started) start();
    }

    //    
    currentControls = {
      pause: () => applyPause(true),
      resume: () => applyPause(false),
      isPaused: () => paused,
      start: () => {
        if (started) return;
        startRequested = true;
        ensureAudioUnlocked();
        void audio.startBgm();
        if (typeof onGameStart === 'function') { try { onGameStart(); } catch {} }
        if (isReady && !started) start();
      },
      restart: () => {
        try { sessionStorage.setItem('hopper_autostart', '1'); } catch {}
        location.reload();
      },
    };

    //        
    return () => {
      try {
        document.removeEventListener('visibilitychange', onVis);
        window.removeEventListener('keydown', onKeyDown);
        canvas.removeEventListener('pointerdown', onTap as any);
        canvas.removeEventListener('click', onClickCapture as any, true);
        try { vv?.removeEventListener('resize', onVV); } catch {}
        try { vv?.removeEventListener('scroll', onVV); } catch {}
        ro.disconnect();
        if (started) cancelAnimationFrame(rafId);
        try { audio.stopBgm(); } catch {}
      } finally {
        currentControls = null;
        try { worker.terminate(); } catch {}
      }
    };
  }

  // ------------------  fallback- ------------------
  const msg = document.createElement('div');
  msg.setAttribute('role', 'status');
  msg.textContent = ' :      .';
  canvas.insertAdjacentElement('afterend', msg);
  return () => {
    try { msg.remove(); } catch {}
  };
}
