import { createApp } from 'vue';
import { registerSW } from 'virtual:pwa-register';

import App from './App.vue';

import './assets/scss/main.scss';
import { createPinia } from 'pinia';

const updateSW = registerSW({
  immediate: true,
  onNeedRefresh() {
    //     
    const bar = document.createElement('div');
    bar.style.position = 'fixed';
    bar.style.left = '0';
    bar.style.right = '0';
    bar.style.bottom = '0';
    bar.style.zIndex = '9999';
    bar.style.display = 'flex';
    bar.style.gap = '8px';
    bar.style.alignItems = 'center';
    bar.style.justifyContent = 'center';
    bar.style.padding = '10px 12px';
    bar.style.background = 'rgba(15,23,42,0.96)';
    bar.style.color = '#fff';
    bar.style.font = '14px/1.4 system-ui, -apple-system, Segoe UI, Roboto, Arial';
    bar.textContent = ' .  ?';

    const btn = document.createElement('button');
    btn.textContent = '';
    btn.style.marginLeft = '12px';
    btn.style.padding = '6px 10px';
    btn.style.background = '#10b981';
    btn.style.color = '#0b1020';
    btn.style.border = 'none';
    btn.style.borderRadius = '4px';
    btn.style.cursor = 'pointer';
    btn.onclick = () => {
      try { updateSW(true); } finally { bar.remove(); }
    };

    const skip = document.createElement('button');
    skip.textContent = '';
    skip.style.marginLeft = '6px';
    skip.style.padding = '6px 10px';
    skip.style.background = '#64748b';
    skip.style.color = '#fff';
    skip.style.border = 'none';
    skip.style.borderRadius = '4px';
    skip.style.cursor = 'pointer';
    skip.onclick = () => bar.remove();

    bar.appendChild(btn);
    bar.appendChild(skip);
    document.body.appendChild(bar);
  },
  onOfflineReady() {
    //    /
    console.log('[PWA]    -');
  },
});

const app = createApp(App);
app.use(createPinia());
app.mount('#app');
